import { Todolist } from '../api/todolistsApi.types';
import { AppDispatch } from 'app/store';
import { todolistsApi } from '../api/todolistsApi';
import { RequestStatus, setAppErrorAC, setAppStatusAC } from 'app/app-reducer';
import { ResultCode } from 'common/enums/enums';
import { handleServerAppError } from 'common/utils/handleServerAppError';
import { handleServerNetworkError } from 'common/utils/handleServerNetworkError';

export type FilterValuesType = 'all' | 'active' | 'completed';

export type DomainTodolist = Todolist & {
  filter: FilterValuesType;
  entityStatus: RequestStatus;
};

const initialState: DomainTodolist[] = [];

// Actions types

export type DeleteTodolistActionType = ReturnType<typeof deleteTodolistAC>;

export type AddTodolistActionType = ReturnType<typeof addTodolistAC>;

export type ChangeTodolistFilterActionType = ReturnType<
  typeof changeTodolistFilterAC
>;

export type UpdateTodolistTitleActionType = ReturnType<
  typeof updateTodolistTitleAC
>;

export type SetTodolistsActionType = ReturnType<typeof setTodolistsAC>;

export type ChangeTodolistEntityStatusActionType = ReturnType<
  typeof changeTodolistEntityStatusAC
>;

type ActionsType =
  | DeleteTodolistActionType
  | AddTodolistActionType
  | ChangeTodolistFilterActionType
  | UpdateTodolistTitleActionType
  | SetTodolistsActionType
  | ChangeTodolistEntityStatusActionType;

export const todolistsReducer = (
  state: DomainTodolist[] = initialState,
  action: ActionsType
): DomainTodolist[] => {
  switch (action.type) {
    case 'SET-TODOLISTS': {
      return action.todolists.map(tl => ({
        ...tl,
        filter: 'all',
        entityStatus: 'idle',
      }));
    }
    case 'DELETE-TODOLIST': {
      return state.filter(tl => tl.id !== action.payload.todolistId);
    }
    case 'ADD-TODOLIST': {
      const newTodolist: DomainTodolist = {
        ...action.payload.todolist,
        filter: 'all',
        entityStatus: 'idle',
      };
      return [newTodolist, ...state];
    }
    case 'CHANGE-TODOLIST-FILTER': {
      const todolist = state.find(tl => tl.id === action.payload.todolistId);
      if (todolist) {
        todolist.filter = action.payload.filter;
      }
      return [...state];
    }
    case 'UPDATE-TODOLIST-TITLE': {
      const todolist = state.find(tl => tl.id === action.payload.id);
      if (todolist) {
        todolist.title = action.payload.title;
      }
      return [...state];
    }
    case 'CHANGE-TODOLIST-ENTITY-STATUS': {
      return state.map(tl =>
        tl.id === action.payload.id
          ? { ...tl, entityStatus: action.payload.entityStatus }
          : tl
      );
    }
    default:
      return state;
  }
};

// Action creators

export const setTodolistsAC = (todolists: Todolist[]) => {
  return { type: 'SET-TODOLISTS', todolists } as const;
};

export const deleteTodolistAC = (todolistId: string) => {
  return { type: 'DELETE-TODOLIST', payload: { todolistId } } as const;
};

export const addTodolistAC = (todolist: Todolist) => {
  return { type: 'ADD-TODOLIST', payload: { todolist } } as const;
};

export const changeTodolistFilterAC = (payload: {
  todolistId: string;
  filter: FilterValuesType;
}) => {
  return {
    type: 'CHANGE-TODOLIST-FILTER',
    payload,
  } as const;
};

export const updateTodolistTitleAC = (payload: {
  title: string;
  id: string;
}) => {
  return {
    type: 'UPDATE-TODOLIST-TITLE',
    payload,
  } as const;
};

export const changeTodolistEntityStatusAC = (payload: {
  id: string;
  entityStatus: RequestStatus;
}) => {
  return { type: 'CHANGE-TODOLIST-ENTITY-STATUS', payload } as const;
};

// Thunk

export const fetchTodolistsTC = () => (dispatch: AppDispatch) => {
  //  Loading
  dispatch(setAppStatusAC('loading'));
  //       (  )
  todolistsApi
    .getTodolists()
    .then(res => {
      const todolists = res.data;
      //  Loading
      dispatch(setAppStatusAC('succeeded'));
      //    (action)    (thunk)
      dispatch(setTodolistsAC(todolists));
    })
    .catch(error => {
      handleServerNetworkError(error, dispatch);
    });
};

export const addTodolistTC = (title: string) => (dispatch: AppDispatch) => {
  dispatch(setAppStatusAC('loading'));
  todolistsApi
    .createTodolist(title)
    .then(res => {
      if (res.data.resultCode === ResultCode.Success) {
        dispatch(addTodolistAC(res.data.data.item));
        dispatch(setAppStatusAC('succeeded'));
      } else {
        handleServerAppError(res.data, dispatch);
      }
    })
    .catch(error => {
      handleServerNetworkError(error, dispatch);
    });
};

export const removeTodolistTC = (id: string) => (dispatch: AppDispatch) => {
  dispatch(setAppStatusAC('loading'));
  dispatch(changeTodolistEntityStatusAC({ id, entityStatus: 'loading' }));
  todolistsApi.removeTodolist(id).then(res => {
    dispatch(setAppStatusAC('succeeded'));
    dispatch(deleteTodolistAC(id));
  });
};

export const updateTodolistTitleTC =
  (arg: { id: string; title: string }) => (dispatch: AppDispatch) => {
    dispatch(setAppStatusAC('loading'));
    todolistsApi
      .updateTodolist(arg)
      .then(res => {
        if (res.data.resultCode === ResultCode.Success) {
          dispatch(updateTodolistTitleAC(arg));
          dispatch(setAppStatusAC('succeeded'));
        } else {
          handleServerAppError(res.data, dispatch);
        }
      })
      .catch(error => {
        handleServerNetworkError(error, dispatch);
      });
  };
